"use client";

import { useRouter } from 'next/navi';
import { gql, useQuery } from '@apollo/client';

const GET_POST = gql`
  query GetPost($id: Int!) {
    post(id: $id) {
      title
      content
      imageUrl
      author {
        name
      }
      createdAt
    }
  }
`;

const BlogPost = () => {
  const router = useRouter();
  const { id } = router.query;
  const { data, loading, error } = useQuery(GET_POST, { variables: { id: Number(id) } });

  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error loading post</p>;

  const { post } = data;

  return (
    <div className="max-w-3xl mx-auto p-4">
      <h1 className="text-3xl font-bold">{post.title}</h1>
      <p className="text-gray-500 mb-4">By {post.author.name} â€¢ {new Date(post.createdAt).toLocaleDateString()}</p>
      <img src={post.imageUrl} alt={post.title} className="w-full h-80 object-cover rounded-lg mb-4" />
      <p className="text-lg">{post.content}</p>
    </div>
  );
};

export default BlogPost;
